  position: fixed;
  top: 0;
}

.preloader {
  width: 180px;
  height: 180px;
  background: url("logo-orange.5f1dea2b.png") center / 120px no-repeat;
  margin: 200px auto auto;
}

.preloader2 {
  width: 150px;
  height: 150px;
  border: 5px solid #f3f3f3;
  border-top-color: #ff6b01;
  border-radius: 50%;
  margin: auto;
  animation: spin 1s ease-in-out infinite;
  position: relative;
  top: -165px;
}

.loader-hidden {
  opacity: 0;
  visibility: hidden;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

