.library__heading-txt {
  font-size: 20px;
  text-align: center;
  color: #ff6b01;
  margin-bottom: 30px;
  list-style-type: none;
}

.library__txt-upper {
  color: #ff6b01;
  text-align: center;
  margin-bottom: 20px;
 list-style-type: none;
}

.library__txt-down {
  color: #212121;
  text-align: center;
  margin-bottom: 50px;
  list-style-type: none;
}

.nothing-to-show {
    margin: 0 auto;
    display: block;
    transition-property: background-color;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

